html {
  height: -moz-available; /* WebKit-based browsers will ignore this. */
  height: -webkit-fill-available; /* Mozilla-based browsers will ignore this. */
  height: fill-available;
}

#title {
  text-align: center;
  margin-bottom: 20px;
}

body {
  font-family: "Old Standard TT", serif;
  max-width: 360px;
  margin: auto;
  background: black;
  color: white;
  text-decoration: none;
  border: 1px solid black;
  /* box-sizing: border-box; */
  position: relative;
  height: -moz-available; /* WebKit-based browsers will ignore this. */
  height: -webkit-fill-available; /* Mozilla-based browsers will ignore this. */
  height: fill-available;
  /* overflow: hidden; */
  touch-action: none !important;
  user-select: none !important;
  -webkit-user-select: none !important;
}

#face-color-slider {
  margin-top: 10px;
  /* animation: slider 3s alternate infinite; */
  width: 100vw;
  max-width: 360px;
  /* transform: rotate(90deg); */
}

input[type="range"] {
  height: 10px;
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
  background: transparent;
}
input[type="range"]:focus {
  outline: none;
}
input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 20px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 0px skyblue;
  /* background: #ffffff; */
  border-radius: 0px;
  border: 2px solid skyblue;
}
input[type="range"]::-webkit-slider-thumb {
  box-shadow: 0px 4px 26px 7px #00f0ff;
  border: 2px solid skyblue;
  height: 40px;
  width: 20px;
  border-radius: 0px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -12px;
}
input[type="range"]:focus::-webkit-slider-runnable-track {
  /* background: #ffffff; */
}
input[type="range"]::-moz-range-track {
  width: 100%;
  height: 20px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 4px 26px 7px #00f0ff;
  /* background: #ffffff; */
  border-radius: 0px;
  border: 2px solid skyblue;
}
input[type="range"]::-moz-range-thumb {
  box-shadow: 0px 4px 26px 7px #00f0ff;
  border: 2px solid skyblue;
  height: 40px;
  width: 20px;
  border-radius: 0px;
  /* background: #ffffff; */
  cursor: pointer;
}
input[type="range"]::-ms-track {
  width: 100%;
  height: 20px;
  cursor: pointer;
  animate: 0.2s;
  border-color: transparent;
  color: transparent;
}
input[type="range"]::-ms-fill-lower {
  /* background: #ffffff; */
  border: 2px solid skyblue;
  border-radius: 0px;
  box-shadow: 0px 4px 26px 7px #00f0ff;
}
input[type="range"]::-ms-fill-upper {
  /* background: #ffffff; */
  border: 2px solid skyblue;
  border-radius: 0px;
  box-shadow: 0px 4px 26px 7px #00f0ff;
}
input[type="range"]::-ms-thumb {
  margin-top: 1px;
  box-shadow: 0px 4px 26px 7px #00f0ff;
  border: 2px solid skyblue;
  height: 40px;
  width: 20px;
  border-radius: 0px;
  /* background: #ffffff; */
  cursor: pointer;
}

a {
  text-decoration: none;
  color: skyblue;
}

a:visited {
  text-decoration: none;
}
a:hover {
  /* text-decoration: underline; */
  cursor: pointer;
}

.section-download {
  margin-top: 16px;
  justify-content: space-between;
}

.section-brain {
  justify-content: space-evenly;
}

#title {
  margin-bottom: 0px;
}
#credits {
  margin-top: 0px;
}

canvas {
  max-width: 100%;
  max-height: calc(100% - 30px);
  margin: auto;
}

#myCanvas {
  border: 1px solid skyblue;
  width: 100%;
  /* this 'scales' the canvas, not changing the actual size */
}

button {
  color: skyblue;
  border: 1px solid skyblue;
  background: black;
  box-shadow: 0px 4px 20px 8px #00f0ff;
  backdrop-filter: blur(10px);
  margin: 0 5px 5px 0;
  padding: 2px 4px;
}

button:active {
  background: black;
  color: white;
  box-shadow: unset;
  transform: translate(1px, 1px);
}

.section {
  display: flex;
}

.brain-container {
  padding: 100px;
  justify-content: center;
}

.slider {
  display: flex;
  justify-content: center;
}
